<template>
    <div class="photo_block container">
        <div class="block_title">
            <h2>Photos</h2>
        </div>
        <div class="block_content">
            <div class="grid">
                <div
                    class="pic_box"
                    v-for="(photo, index) in this.photoList"
                    :key="index"
                    :style="{backgroundImage: 'url(' + photo +' )'}"
                    @click="zoomImage(index)">
                </div>
            </div>
        </div>
        <div class="block_bottom">
            <button @click="prev"><i class="fas fa-chevron-left"></i></button>
            <img src="">
            <button @click="next"><i class="fas fa-chevron-right"></i></button>
            <button @click="close"><i class="fas fa-times"></i></button>
        </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      photoList: [
        'https://news.cgtn.com/news/78457a4d7a637a6333566d54/img/f00ee2af-ae11-4067-92b0-79307fc853a1.jpg',
        'https://static.themoscowtimes.com/image/1360/e5/b316370e9d8341d4b4ea3133ecc3979b.jpg',
        'https://the18.com/sites/default/files/feature-images/GettyImages-988997912.jpg',
        'https://www.watson.ch/imgdb/856a/Qx,E,0,235,5184,2916,2160,1440,864,576/8900540075227540',
        'https://media.contentapi.ea.com/content/dam/ea/easports/fifa/news-media/2018/july/fifa-wc-body-image3.jpg.adapt.crop16x9.1455w.jpg',
        'https://m.media-amazon.com/images/M/MV5BOTlmMjk2ZjItMzRjYS00MDg1LTljZmItNmEzZjkyZGU1NWViXkEyXkFqcGdeQXVyNDg4MjkzNDk@._V1_SY400_CR98',
        'https://www.arabianbusiness.com/public/images/2018/07/02/fifa-round-16-spain-russia-1.jpg',
        'https://dneegypt.nyc3.digitaloceanspaces.com/2018/09/russia-fans_1m2zyz80v247g1bt4b40aj0hno.jpg',
        'https://img.thedailybeast.com/image/upload/c_crop,d_placeholder_euli9k,h_1440,w_2560,x_0,y_0/dpr_2.0/c_limit,w_740/fl_lossy,q_auto/v1529400460/180617-nemtsova-russia-hero_kju5br',
        'https://static.rfs.ru/albums/5088/5b229b628bbe9_1920.jpg',
        'https://condenast-media.gcdn.co/tatler/9f7c290b3be02b84b4595d7785caef12.jpg/476e6cee/o/t4072x2715',
        'https://e0.365dm.com/18/07/1600x900/skysports-russia-croatia_4356203.jpg?20180707193737'
      ],
      currentIndex: ''
    }
  },
  methods: {
    zoomImage: function (index) {
      const showBlock = document.querySelector('.photo_block .block_bottom')
      showBlock.style.display = 'flex'
      showBlock.querySelector('img').setAttribute('src', this.photoList[index])
      this.currentIndex = index
    },
    next: function () {
      if (this.currentIndex < this.photoList.length - 1) {
        this.currentIndex++
      } else {
        this.currentIndex = 0
      }
      const showBlock = document.querySelector('.photo_block .block_bottom')
      showBlock.style.display = 'flex'
      showBlock.querySelector('img').setAttribute('src', this.photoList[this.currentIndex])
    },
    prev: function () {
      if (this.currentIndex > 0) {
        this.currentIndex--
      } else {
        this.currentIndex = this.photoList.length - 1
      }
      const showBlock = document.querySelector('.photo_block .block_bottom')
      showBlock.style.display = 'flex'
      showBlock.querySelector('img').setAttribute('src', this.photoList[this.currentIndex])
    },
    close: function () {
      const showBlock = document.querySelector('.photo_block .block_bottom')
      showBlock.style.display = 'none'
    }
  }
}
</script>
