<template>
    <div class="container">
        <div class="article_block">
            <div class="block_title">
                <div v-if="newsInfo.heroImage != ''" class="pic_box" :style="{ backgroundImage: 'url(' + newsInfo.heroImage + ')' }"></div>
                <h2>{{newsTitle}}</h2>
                <span>Published Date: {{newsInfo.date}}</span>
                <span v-if="newsInfo.author != null"> By {{newsInfo.author}}</span>
            </div>
            <hr>
            <div class="block_content">
                <div v-for="content in newsInfo.text" v-html="content" :key="content">
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      newsInfo: {},
      newsTitle: ''
    }
  },
  computed: {
    newsId () {
      return this.$route.params.newsId
    }
  },
  watch: {
    newsId: async function (val) {
      await this.fetchNewsInfo(val)
    }
  },
  methods: {
    async fetchNewsInfo (newsId) {
      const { data } = await this.$http.get('/news/detail/', { params: { id: newsId } })
      this.newsInfo = data.news
      this.newsTitle = data.share.title
    }
  },
  async mounted () {
    await this.fetchNewsInfo(this.newsId)
  }
}
</script>
<style lang="sass">

  .article_block
    background-color: $color-white
    padding: 3rem 3rem 10rem
    .block_title
      .pic_box
        min-height: 65vh
        background-size: cover
        background-position: center
        margin: -3rem
      h2
        margin-top: 2.75rem
        padding-bottom: .5rem
      span
        color: hsl(0,0,50)
    hr
      margin: 1.5rem 0 3rem
    .block_content
      font-size: 1.25rem
      div + div
        margin-top: 2rem
</style>
