<script>
export default {
  mounted () {
    const carousel = document.getElementById('carousel')
    carousel.addEventListener('slide.bs.carousel', this.getCarouselIndex)

    const player = document.querySelector('.carousel-item.active img')
    this.imageMovement(player)
  },
  watch: {
    '$store.state.carouselIndex': function (val) {
      const index = parseInt(val) + 1
      const player = document.querySelector('.carousel-item:nth-child(' + index + ') img')
      this.imageMovement(player)
    }
  },
  methods: {
    getCarouselIndex (ev) {
      this.$store.state.carouselIndex = ev.to
    },
    imageMovement (player) {
      player.addEventListener('mousemove', (e) => {
        player.style.marginBottom = e.offsetY / (window.innerHeight / 40) * (-1) + 'px'
        player.style.marginLeft = e.offsetX / (window.innerWidth / 40) + 'px'
      })
    }
  }
}
</script>
