Vue.filter('translateText', function (value) {
  value = value.replace(/Offensives Mittelfeld/g, 'Attacking midfield')
  return value
})

Vue.filter('formatBirthday', function (value) {
  const monthNames = ['Jan.', 'Feb.', 'Mar.', 'Apr.', 'May.', 'Jun.',
    'Jul.', 'Aug.', 'Sep.', 'Oct.', 'Nov.', 'Dec.'
  ]
  const birthArr = value.split('.')
  const birthMonth = parseInt(birthArr[1])
  return birthArr[0] + ' ' + monthNames[birthMonth - 1] + ' ' + birthArr[2]
})

Vue.filter('formatMeters', function (value) {
  return parseFloat(value.replace(',', '.')) * 100
})

Vue.filter('formatName', function (value) {
  const name = value.split(' ')
  return name[0] + '\n' + name[1].toUpperCase()
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
