<template>
  <div class="player_box">
    <div class="box_title">
      <h3><pre>{{profile[this.$store.state.carouselIndex].playerName | formatName}}</pre></h3>
            <img :src="profile[this.$store.state.carouselIndex].playerImage" alt="">
    </div>
    <div class="box_content">
      <div class="info_item">
        <div class="item_title">
          <h5>Age:</h5>
        </div>
        <div class="item_content">
          <p>{{profile[this.$store.state.carouselIndex].age}}</p>
        </div>
      </div>
      <div class="info_item">
        <div class="item_title">
          <h5>Birth:</h5>
        </div>
        <div class="item_content">
          <p>{{profile[this.$store.state.carouselIndex].dateOfBirth | formatBirthday}}</p>
        </div>
      </div>
      <div class="info_item">
        <div class="item_title">
          <h5>Height:</h5>
        </div>
        <div class="item_content">
          <p>{{profile[this.$store.state.carouselIndex].height | formatMeters}} CM</p>
        </div>
      </div>
      <div class="info_item">
        <div class="item_title">
          <h5>International Games:</h5>
        </div>
        <div class="item_content">
          <p>{{profile[this.$store.state.carouselIndex].internationalGames}}</p>
        </div>
      </div>
      <div class="info_item">
        <div class="item_title">
          <h5>Position:</h5>
        </div>
        <div class="item_content">
          <p>{{profile[this.$store.state.carouselIndex].playerMainPosition}}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      playerList: ['15570', '263918', '48067', '15076', '129096'],
      profile: []
    }
  },
  created () {
    this.getData(this.playerList)
  },
  methods: {
    async getData (playerList) {
      for (let index = 0; index < playerList.length; index++) {
        const { data } = await this.$http.get('/players/get-profile', { params: { id: playerList[index] } })
        this.profile.push(data.playerProfile)
      }
    }
  }
}
</script>

